<template>
    <div class="recommend-box">
        <header class="header">

            <!-- tab 选项卡 -->
            <ul class="tabs-list">
                <li class="item" v-for="(item, index) in list" :key="index">
                    <a href="javascript:;" @click.prevent="toggleTab(index)" :class="{ active: curId === index }">{{ item.tab }}</a>
                </li>
            </ul>
            <a href="javascript:;" class="recommend-more">查看更多 ></a>
        </header>

        <!-- 推荐内容 -->
        <card :oplaceData="similarOplaceDatas" v-show="similarFlag"></card>
        <card :oplaceData="nearbyOplaceDatas" v-show="nearbyFlag"></card>
    </div>
</template>
<script>

    import Card from '../component/card.vue';
    import Classify from '../component/classify.vue';
    import COMMENT from '../../../comment.js';
    export default {
        data () {
            return {
                similarOplaceDatas: [], // 相似作品推荐
                nearbyOplaceDatas: [], // 附近作品推荐
                list: [
                    { "tab": "相似" },
                    { "tab": "附近" }
                ],
                curId: 0, // 当前选项卡激活状态
                similarFlag: true, // 相似作品的显示标识
                nearbyFlag: false, // 附近作品的显示标识
            }
        },
        created () {
            this.getRecommendDatas();
        },
        methods: {

            // 获取详情页推荐作品信息
            getRecommendDatas () {
                this.similarOplaceDatas = COMMENT.comments;
                console.log(this.similarOplaceDatas)

                // this.$axios.get('../../../comment.js').then(res => {
                //     let recommendDatas = res.data.data;
                //     this.similarOplaceDatas = recommendDatas.slice(0, 3);
                //     this.nearbyOplaceDatas = recommendDatas.slice(3);
                // }).catch(err => {
                //     this.error = err.toString();
                //     throw new Error("this.$axios.get: 无法获取数据");
                // });
            },

            // 切换选项卡
            toggleTab (index) {
                this.curId = index;
                this.similarFlag = !this.similarFlag;
                this.nearbyFlag = !this.nearbyFlag;

            }
        },
        components: {
            Card,
            Classify
        }
    }
</script>
<style rel="stylesheet/scss" lang="scss" media="screen" scoped>
    
    .recommend-box {
        .header {
            height: 24px;
            margin: {
                left: -14px;
                bottom: 10px;
            }
            .tabs-list {
                .item {
                    float: left;
                    a {
                        width: 55px;
                        text-align: center;
                        font-weight: bold;
                        color: #464646;
                        &:hover {
                            color: #48abff;
                        }
                    }
                    a.active {
                        color: #48abff;
                    }
                }
                .item:first-child {
                    position: relative;
                    &:after {
                        content: '';
                        display: inline-block;
                        width: 1px;
                        height: 12px;
                        background: #d6d6d6;
                        position: absolute;
                        top: 4px;
                        right: 0;
                    }
                }
            }
            .recommend-more {
                width: auto;
                font-size: x-small;
                color: #48ABFF;
                line-height: 20px;
                float: right;
            }

        }
    }
</style>


