<template>
    <div class="comment-box">
        <header class="header">
            评论
        </header><!-- /header -->
        <div class="comment-textarea-box">
            <textarea name="评论" placeholder="写下您的看法" class="comment-area"></textarea>
            <o-button :text="'发表'" :classStyle="'main-btn'" class="publish-btn" @btnClick="publishComment"></o-button>
        </div>

        <!-- 有评论时 显示该作品的评论列表-->
        <ul class="list" v-show="oplaceComment.length !== 0">
            <li class="item" v-for="(item, index) in oplaceComment" :key="index">
                <a href="javascript:;" class="commentator-img">
                    <img :src="item.commentatorImg" alt="" />
                </a>
                <div class="content">
                    <div class="comment-header">
                        <a href="javascript:;" class="nickname">{{ item.nickName }}</a>
                        <div class="right-box">
                            <span class="comment-time">{{ item.time }}</span>
                            <span class="interaction-box">
                                <a href="javascript:;" class="reply-btn">{{ item.reply.length }}</a>
                                <a href="javascript:;" :id="item.id" class="praise-btn" :class="{ praised: item.praiseState }"  ref="praise" @click.prevent="givePraise(item)">{{ item.praise }}</a>
                            </span>
                        </div>
                    </div>
                    <p class="comment-content">
                        {{ item.content }}
                    </p>
                    <div class="reply-box" ref="replys" v-if="item.reply.length > 0">
                        <ul class="reply-list">
                            <li class="reply-item" v-for="(value, index) in item.reply" :key="index">
                                <span class="reply-name">{{ value.nickName }}:</span>
                                <p>{{ value.content }}</p>
                                <span class="reply-time">{{ value.time }}</span>
                            </li>
                        </ul>
                    </div>
                    <div v-if="item.reply.length > 3">
                        <a href="javascript:;" :data-num="index" class="check-more" @click.prevent="checkMore(index)" ref="seeMore">查看全部{{ item.reply.length - 3 }}条评论</a>
                        <a href="javascript:;" :data-num="index" class="check-more hide" ref="hideMore" @click.prevent="hideMore(index)">收起...</a>
                    </div>
                </div>
            </li>
        </ul>

        <!-- 当没有评论时 来抢沙发吧 -->
        <div v-show="oplaceComment.length === 0" class="no-comments">
            <p>快来抢沙发吧...</p>
        </div>

        <!-- 发表评论成功弹窗 -->
        <small-pop :text="'发表成功'" v-if="publishFlag"></small-pop>

    </div>
</template>
<script>
    
    import OButton from '../component/button.vue';
    import SmallPop from '../component/smallPop.vue';

    export default {
        data () {
            return {
                seeMoreFlag: true, // 点击查看更多
                publishFlag: false, // 发表评论成功弹窗标识
            }
        },
        created () {
        },
        methods: {

            toggleMore (index) {

                let seeMore = this.$refs.seeMore;
                let hideMore = this.$refs.hideMore;
                seeMore.some(item => {

                    if (item.dataset.num == index) {

                        item.classList.toggle('hide');
                        return true;
                    }
                });

                hideMore.some(item => {

                    if (item.dataset.num == index) {

                        item.classList.toggle('hide');
                        return true;
                    }
                });
            },

            // 点击查看更多评论回复
            checkMore (index) {
                this.$refs.replys[index ].classList.toggle('see-more');
                this.toggleMore (index);
            },

            // 收起
            hideMore (index) {
                this.$refs.replys[index ].classList.toggle('see-more');
                this.toggleMore (index);
            },

            // 点赞
            givePraise (item) {

                item.praiseState = !item.praiseState;

                if (item.praiseState) {
                    item.praise = parseInt(item.praise) + 1;
                } else {
                    item.praise = parseInt(item.praise) - 1;
                }

                this.oplaceComment.some(value => {

                    if (value.id === item.id) {
                        value = item;
                    }
                    return true;
                });

                // 保存至本地该作品的信息
                localStorage.setItem('oplaceComment', JSON.stringify(this.oplaceComment)); 
                this.$store.commit('COUNTPRAISE', item); 
            },

            // 发表评论
            publishComment () {
                this.publishFlag = !this.publishFlag;
                var _this = this;

                // 800ms 后消失
                setTimeout(function () {
                    _this.publishFlag = false;
                }, 800);
            }
        },
        components: {
            OButton,
            SmallPop
        },
        props: ["oplaceComment"]
    }
</script>
<style rel="stylesheet/scss" lang="scss" media="screen" scoped>

    /*评论*/
    .comment-box {
        margin-top: 50px;
        overflow: visible;
        .header {
            border-left: 3px solid #343434;
            padding-left: 8px;
            font-weight: bold;
            line-height: 16px;
        }
        .comment-textarea-box {
            padding: {
                right: 94px;
                bottom: 10px;
            }
            position: relative;
            margin-top: 15px;
            .comment-area {
                display: block;
                resize: none;
                width: 100%;
                height: 60px;
                border-radius: 5px;
                background-color: #F4F4F4;
                padding: 8px 10px;
                font-size: 14px;
                font-weight: bold;
                overflow-y: auto;
                color: #343434;
            }
            .publish-btn {
                width: 84px;
                height: 60px;
                position: absolute;
                top: 0;
                right: 0;
                z-index: 0;
            }
        }

        // 评论列表
        .list {
            .item {
                padding: {
                    top: 20px;
                    left: 54px;
                }
                position: relative;
                height: auto;
                .commentator-img { // 评论人头像
                    width: 42px;
                    height: 42px;
                    border-radius: 50%;
                    overflow: hidden;
                    position: absolute;
                    top: 18px;
                    left: 0;
                }
                .content { // 评论区内容
                    padding-bottom: 20px;
                    border-bottom: 1px solid #f4f4f4;
                    .comment-header {
                        height: 24px;
                        .nickname {
                            font-weight: bold;
                            display: inline;
                        }
                        .right-box {
                            float: right;
                            font-size: x-small;
                            .comment-time {
                                line-height: 24px;
                                float: right;
                                color: #606060;
                            }
                            .interaction-box {
                                height: 100%;
                                float: left;
                                padding-top: 4px;
                                user-select: none;
                                /* display: none; */
                                a {
                                    display: inline-block;
                                    width: auto;
                                    color: #48ABFF;
                                    padding-left: 23px;
                                    background: url(../../../static/images_icon/sprite.png) no-repeat;
                                    background-size: auto 290px;
                                    float: left;
                                    margin-right: 18px;
                                }
                                .reply-btn {
                                    background-position: -274px -80px;
                                }
                                .praise-btn {
                                    background-position: -273px -271px;
                                }
                                .praised {
                                    background-position: -95px -175px;
                                }
                            }
                        }
                    }
                    .comment-content {
                        font-size: small;
                        margin: {
                            top: 3px;
                            bottom: 10px;
                        }
                    }
                    .reply-box {
                        width: 100%;
                        height: 90px;
                        background-color: #F4F4F4;
                        padding: 12px 12px 8px;
                        border-radius: 4px;
                        margin-bottom: 26px;
                        overflow: hidden;
                        .reply-list {
                            .reply-item {
                                position: relative;
                                padding-right: 60px;
                                margin-bottom: 5px;
                                font-size: small;
                                line-height: 20px;
                                .reply-name {
                                    float: left;
                                    font-weight: bold;
                                }
                                .reply-time {
                                    height: 100%;
                                    line-height: 16px;
                                    font-size: 11px;
                                    font-weight: normal;
                                    color: #606060;
                                    position: absolute;
                                    top: 0;
                                    right: 0;
                                }
                            }
                        }
                    }
                    .see-more {
                        height: auto;
                    }
                    .check-more {
                        width: 140px;
                        text-align: center;
                        height: 20px;
                        font: {
                            weight: bold;
                            size: x-small;
                        }
                        margin: 0 auto;
                    }
                    .hide {
                        display: none;
                    }
                }
            }
        }

        // 没有评论时
        .no-comments {
            height: 200px;
            border-radius: 3px;
            line-height: 12;
            background: #f2f2f2;
            text-align: center;
            color: #5a5858;
            margin-top: 20px;
        }
    }
</style>


