<template>

    <!--图片详情页内容-->
    <div class="content">
        <div class="leftbox">
            <a href="javascript:;" :title="oplace.title" class="opus">
                <span v-if="oplace.type === 'mp3'" class="">
                    <img src="static/images_icon/audio_img.png" alt="音频的默认图片" />
                </span>
                <span v-else>
                    <img src="" v-lazy="oplace.detailImg" />
                </span>
                <span v-if="oplace.type === 'mp3' || oplace.type === 'mp4'" class="play-icon">
                    <img src="static/images_icon/play_icon.png" alt="播放按钮" />
                </span>
                <div class="buttons">
                    <o-button :text="'删除'" :classStyle="'del-btn'" class="gather"></o-button>
                    <o-button :text="textObject[oplace.state]" :classStyle="gatherObject[oplace.state]" class="gather"></o-button>
                </div>
            </a>
            <h3 class="title">{{ oplace.header }}</h3>
            <comment :oplaceComment="oplaceComment"></comment>
        </div>

        <div class="rightbox">

            <!-- 下载格式 -->
            <div class="upload-information">
                <h2 class="title">下载：</h2>

                <!-- 图片详情页中的下载格式 -->
                <div class="content-wrapper">

                    <!-- 图片下载信息 -->
                    <ul v-show="oplace.type === 'jpg'">
                        <li class="download-item" v-for="(item, index) in pictureDownload" :key="index">
                            <span class="resolution">{{ item.resolution }}</span>
                            <span class="size">{{ item.size }}</span>
                            <o-button :text="item.text" :classStyle="'main-btn'" class="download-btn" @btnClick="item.event"></o-button>
                        </li>
                    </ul>

                    <!-- 视频下载信息 -->
                    <ul v-show="oplace.type === 'mp4'">
                        <li class="download-item" v-for="(item, index) in videoDownload" :key="index">
                            <span class="resolution">{{ item.resolution }}</span>
                            <span class="size">{{ item.size }}</span>
                            <o-button :text="item.text" :classStyle="'main-btn'" class="download-btn" @btnClick="item.event"></o-button>
                        </li>
                    </ul>

                    <!-- 音频下载信息 -->
                    <ul v-show="oplace.type === 'mp3'">
                        <li class="download-item" v-for="(item, index) in audioDownload" :key="index">
                            <span class="resolution">{{ item.resolution }}</span>
                            <span class="size">{{ item.size }}</span>
                            <o-button :text="item.text" :classStyle="'main-btn'" class="download-btn" @btnClick="item.event"></o-button>
                        </li>
                    </ul>
                </div>
            </div>

            <!-- 作品简介 -->
            <div class="upload-information opus-brief">
                <h2 class="title">图片信息：</h2>
                <div class="content-wrapper">
                    <ul class="brief-list">
                        <li class="brief-item">
                            <h3 class="title">图片作者：</h3>
                            <a href="javascript:;" class="box">
                                <img src="static/images_header/header6.jpg" class="author-head">
                                <span class="information-text">XX作者名</span>
                                <span class="attestation"></span>
                            </a>
                            <o-button :text="'打赏作者'" :classStyle="'main-btn'" class="reward-btn" @btnClick="showRewardPopup"></o-button>
                        </li>
                        <li class="brief-item">
                            <h3 class="title">拍摄地点：</h3>
                            <a href="javascript:;" class="box">
                                <span class="attestation location-icon"></span>
                                <span class="information-text">桂林·XX公园</span>
                            </a>
                        </li>
                        <li class="brief-item">
                            <h3 class="title">图片格式：</h3>
                            <a href="javascript:;" class="box">
                                <span class="information-text">.{{ oplace.type }}</span>
                            </a>
                        </li>
                        <li class="brief-item">
                            <h3 class="title">授权：</h3>
                            <p class="information-text">
                                <span>可个人使用</span>
                                <span>可商业使用</span>
                                <span>可编辑</span>
                                <span>不需要归属</span>
                            </p>
                            <a href="javascript:;" class="intellectual-btn">了解授权知识</a>
                        </li>
                    </ul>
                </div>
                <a @click.prevent="showReportPopup" class="report-box">举报</a>
            </div>

            <!-- 推荐作品 -->
            <detail-recommend></detail-recommend>
        </div>
        <!-- 下载支付弹窗 -->
        <popup class="downloadPopup" v-if="downloadFlag">
            <h2 slot="header" class="download-title">
                <span class="pay-icon"></span>
                支付
            </h2><!-- /header -->
            <div slot="content" class="downloadContent">
                <pay-tabs>
                    <p slot="pay-message" class="text">支付授权费：<strong class="money">283.22</strong>元/年 </p>
                </pay-tabs>
                <div class="pay-explain">
                    <h3 class="title">授权说明：</h3>
                    <p class="pay-tips">购买后您将享有此作品的几乎所有使用权限，包括以商业为目的的使用授权和对该作品的再次编辑的权限，但不包括直接售卖、转卖此作品的权限，且本作品仅可供您个人使用，不得以他人或公司名义使用，如需以公司名义使用本作品请先 <router-link tag="a" to="/account/upgrade" class="link">升级为企业号</router-link> 。详细条款说明参见《<a href="javascript:;" @click.prevent="showAuthorPopup" class="link">原点使用协议</a>》</p>
                </div>
                <div class="pay-explain special-explain">
                    <h3 class="title">注意：</h3>
                    <p class="pay-tips">本站所有授权明细均显示在创作者的个人账户中，如发现任何侵权行为本站将全力支持作者维权，以最大限度的维护原创作者的知识产权。</p>
                </div>
            </div>
            <input type="button" name="" class="close-btn" slot="close" @click="closePopup" />
        </popup>

        <!-- 打赏作者弹窗 -->
        <popup class="downloadPopup" v-if="rewardFlag">
            <h2 slot="header" class="download-title">
                <span class="pay-icon"></span>
                打赏作者
            </h2><!-- /header -->
            <div slot="content" class="rewardContent">
                <div class="reward-author-box">
                    <div class="header">
                        <img src="static/images_header/header6.jpg" class="reward-author-img">
                    </div>
                    <div class="name">
                        <p class="author-name">XX昵称</p>
                        <span class="attestation"></span>
                    </div>
                </div>
                <pay-tabs>
                    <p slot="pay-message" class="text">打赏的费用将全部给予作品的原作者，平台不收取任何费用。</p>
                </pay-tabs>
            </div>
            <input type="button" name="" class="close-btn" slot="close" @click="closeRewardPopup" />
        </popup>

        <!-- 举报作品弹窗 -->
        <popup class="reportdPopup" v-if="reportFlag">
            <h2 slot="header" class="download-title">
                <span class="pay-icon"></span>
                举报
            </h2><!-- /header -->
            <div slot="content" class="reportContent">
                <!--弹框内容-->
                <div class="report-opus-box">
                    <span class="report-opus" v-if="oplace.type === 'mp3'">
                        <img src="static/images_icon/audio_img.png" alt="音频的默认图片" />
                    </span>
                    <span class="report-opus" v-else>
                        <img src="" v-lazy="oplace.detailImg" />
                    </span>
                    <h2 class="title">{{ oplace.header }}</h2>
                </div>
                <select class="report-reason">
                    <option :value="item" v-for="(item, index) in reportRespons" :key="index">{{ item }}</option>
                </select>
                <textarea placeholder="请输入举报的具体理由" class="report-reason-input"></textarea>
                <o-button :text="'提交'" :classStyle="'main-btn'" class="report-btn"></o-button>
            </div>
            <input type="button" name="" class="close-btn" slot="close" @click="closeReportdPopup" />
        </popup>

        <!-- 圆点协议弹窗 -->
        <authorize v-if="authorFlag" @showAuthorPopup="showAuthorPopup"></authorize>
    </div>
</template>
<script>

    import OButton from '../component/button.vue';
    import Comment from './comment.vue';
    import DetailRecommend from './detailRecommend.vue';
    import Popup from '../component/popup.vue';
    import PayTabs from '../component/payTabs.vue';
    import Authorize from '../Popup/Authorize/authorize.vue';
    export default {
        data () {
            return {
                oplace: null, // 存储作品信息
                gatherObject: {
                    '1': "gather-btn",
                    '-1': "gray-btn"
                },
                textObject: {
                    '1': "采集",
                    '-1': "已采集"
                },
                oplaceComment: null, //存储的评论信息
                pictureDownload: [ // 图片下载信息
                    { "resolution": "800px * 1200px", "size": "15Mb", "text": "下载", event: this.download },
                    { "resolution": "1200px * 3600px", "size": "135Mb", "text": "￥30", event: this.payDownload }
                ],
                audioDownload: [ // 音频下载信息
                    { "resolution": "128k bit/s", "size": "10Mb", "text": "￥50", event: this.payDownload }
                ],
                videoDownload: [ // 视频下载信息
                    { "resolution": "800px * 1200px", "size": "100Mb", "text": "￥50", event: this.payDownload }
                ],
                downloadFlag: false, // 支付弹窗的标识
                authorFlag: false, // 圆点协议弹窗标识
                rewardFlag: false, // 打赏作者弹窗标识
                reportRespons: ["举报理由请选择", "涉嫌侵权", "涉黄涉赌", "违法乱纪", "其它原因"], // 举报理由
                reportFlag: false, // 举报弹窗标识
            }
        },
        created() {
            this.getOplaceData();
        },
        methods: {

            // 获取本地存储的作品信息 评论信息
            getOplaceData () {
                this.oplace = JSON.parse(localStorage.getItem("oplace"));
                this.oplaceComment = JSON.parse(localStorage.getItem("oplaceComment")) || [];
            },

            // 支付下载
            payDownload () {
                this.downloadFlag = !this.downloadFlag;
            },

            // 下载
            download () {
                confirm("下载吗？");
            },

            // 关闭下载弹窗
            closePopup () {
                this.downloadFlag = !this.downloadFlag;
            },

            //  展示圆点协议弹窗
            showAuthorPopup () {
                this.closePopup ();

                this.authorFlag = !this.authorFlag;
            },

            // 展示打赏作者弹窗
            showRewardPopup () {
                this.rewardFlag = !this.rewardFlag;
            },

            // 关闭打赏作者弹窗
            closeRewardPopup () {
                this.rewardFlag = !this.rewardFlag;
            },

            // 展示举报弹窗
            showReportPopup () {
                this.reportFlag = !this.reportFlag;
            },

            // 关闭举报弹窗
            closeReportdPopup () {
                this.reportFlag = !this.reportFlag;
            }
        },
        components: {
            OButton,
            Comment,
            DetailRecommend,
            Popup,
            PayTabs,
            Authorize
        }
    }
</script>
<style rel="stylesheet/scss" lang="scss" media="screen" scoped>
    
    .content {
        min-width: 1040px;
        max-width: 1240px;
        padding-top: 10px;

        /*左边部分*/
        .leftbox {
            width: 65%;
            float: left;
            .opus {
                position: relative;
                .play-icon {
                    width: 25px;
                    height: auto;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    margin: {
                        left: -12.5px;
                        top: -18px;
                    }
                    z-index: 0
                }
                .buttons {
                    position: absolute;
                    top: 12px;
                    left: 0;
                    width: 100%;
                    display: none;
                    .gather {
                        width: 69px;
                        height: 34px;
                        float: right;
                        margin-right: 10px;
                    }
                }
                &:hover {
                    .buttons {
                        display: block;
                    }
                }
            }
            .title {
                margin-top: 10px;
            }
        }

        /*右边部分*/
        .rightbox {
            width: 32%;
            float: right;

            // 下载格式
            .upload-information {
                margin-bottom: 36px;
                overflow: hidden;
                h2.title {
                    font-size: 14px;
                    line-height: 20px;
                    margin-bottom: 5px;
                    padding-left: 2px;
                }
                .content-wrapper {
                    background-color: #F4F4F4;
                    border-radius: 5px;
                    padding: 20px 15px 5px;
                    overflow: hidden;
                    .download-item {
                        height: 34px;
                        margin-bottom: 15px;
                        span.size, span.resolution {
                            height: 100%;
                            font-size: 13px;
                            color: #626262;
                            line-height: 34px;
                            font-weight: bold;
                            float: left;
                        }
                        span.resolution {
                            margin-right: 20px;
                        }
                        .download-btn {
                            width: 72px;
                            height: 34px;
                            background: url(../../../static/images_icon/sprite.png) center center no-repeat #48ABFF;
                            background: {
                                position: -332px -136px;
                                size: auto 360px;
                            }
                            padding: {
                                left: 31px;
                                right: 13px;
                            }
                            float: right;
                        }
                    }
                }
            }

            // 作品简介
            .opus-brief {
                .brief-list {
                    .brief-item {
                        height: auto;
                        min-height: 24px;
                        margin-bottom: 15px;
                        overflow: hidden;
                        h3.title {
                            height: 100%;
                            line-height: 24px;
                            font-size: 13px;
                            color: #626262;
                            float: left;
                        }
                        .box {
                            width: auto;
                            float: left;
                            .author-head {
                                width: 20px;
                                height: 20px;
                                border-radius: 50%;
                                float: left;
                                margin: {
                                    left: 5px;
                                    top: 1px;
                                }
                            }
                            .attestation {
                                width: 18px;
                                height: 18px;
                                background: url(../../../static/images_icon/sprite.png) center center no-repeat;
                                background: {
                                    position: -29px -85px;
                                    size: auto 280px;
                                }
                                border-radius: 50%;
                                float: left;
                                margin: {
                                    left: 5px;
                                    top: 3px;
                                }
                            }
                            .location-icon {
                                background: {
                                    position: -76px -279px;
                                    size: auto 340px;
                                }
                            }
                        }
                        .information-text {
                            line-height: 24px;
                            font: {
                                size: small;
                                weight: bold;
                            }
                            color: #626262;
                            float: left;
                            margin-left: 5px;
                        }
                        .reward-btn {
                            height: 23px;
                            color: #48ABFF;
                            font-size: small;
                            padding: {
                                left: 20px;
                                right: 6px;
                            }
                            background: url(../../../static/images_icon/sprite.png) center center no-repeat;
                            background: {
                                position: -266px -172px;
                                size: auto 280px;
                            }
                            border: 1px solid #48ABFF;
                            float: right;
                        }
                        .intellectual-btn {
                            width: auto;
                            line-height: 24px;
                            color: #707070;
                            font-size: x-small;
                            padding: {
                                left: 24px;
                                right: 6px;
                            }
                            background: url(../../../static/images_icon/sprite.png) center center no-repeat;
                            background: {
                                position: -297px -220px;
                                size: auto 320px;
                            }
                            border-radius: 3px;
                            float: right;
                        }
                    }
                }
            }
            
            // 举报
            .report-box {
                width: 42px;
                float: right;
                padding: {
                    top: 5px;
                    left: 18px;
                }
                line-height: 14px;
                font: {
                    size: x-small;
                    weight: bold;
                }
                color: #A8A8A8;
                background: url(../../../static/images_icon/sprite.png) center center no-repeat;
                background: {
                    position: -268px -240px;
                    size: auto 320px;
                }
            }
        }

        // 下载的弹窗
        .downloadPopup, .reportdPopup {
            .download-title {
                padding: {
                    top: 18px;
                    left: 20px;
                }
                font-size: 18px;
                .pay-icon {
                    display: inline-block;
                    width: 30px;
                    height: 30px;
                    background: url(../../../static/images_icon/sprite.png) no-repeat;
                    background: {
                        size: auto 310px;
                        position: -64px -284px;
                    }
                    vertical-align: middle;
                }
            }
            .downloadContent {
                width: 524px;
                height: 495px;
                strong.money {
                    font-size: large;
                }
                .pay-explain {
                    width: 90%;
                    height: auto;
                    margin: 30px auto 20px;
                    overflow: hidden;
                    font: {
                        size: x-small;
                        weight: bold;
                    }
                    line-height: 18px;
                    h3.title {
                        float: left;
                    }
                    p.pay-tips {
                        width: 86%;
                        float: right;
                        a.link {
