        }
        // 举报作品弹窗
        .reportdPopup {
            .pay-icon {
                background-position: -214px -217px!important;
            }
            .reportContent {
                width: 524px;
                height: 380px;
                .report-opus-box {
                    width: 92%;
                    border-radius: 5px;
                    margin: 25px auto 0;
                    padding: 10px 12px;
                    background-color: #F4F4F4;
                    position: relative;
